* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  list-style: none;
}
.img {
  width: 60;
  height: 40;
  margin-right: 12px;
}

.wrapper {
  display: flex;
  align-items: center;
}
.list-item {
  display: flex;
  font-size: 14px;
  align-items: center;
}
.countryName {
  font-size: 36px;
}
